<template>
  <div class="collapse navbar-collapse" id="navbarSupportedContent">
    <button
      class="navbar-toggler navbar-toggler-right close-btn border-0"
      type="button"
      data-toggle="collapse"
      data-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span>
        <i class="fas fa-times"></i>
      </span>
    </button>
    <ul class="navbar-nav mr-auto">
      <router-link
        v-for="(menu,index) in menus"
        :key="index"
        :to="{name: menu.link}"
        v-slot="{ href, navigate, isActive }"
        custom
      >
        <li :class="[ isActive ? 'nav-item active':'nav-item' ]">
          <a class="nav-link" :href="href" @click="navigate" 
          v-if="menu.show || (menu.condition == !(login && login.token && login.token !== ''))">
            {{menu.title}}
            <span class="sr-only">(current)</span>
          </a>
        </li>
      </router-link>
       <li class="nav-item logout-nav">
        <a
          class="nav-link"
          target="_blank"
          :href="this.privacy_policy_url"
        >Privacy Policy</a>
      </li>
       <li class="nav-item logout-nav">
        <a
          class="nav-link"
          target="_blank"
          :href="this.terms_condition_url"
        >Terms & conditions</a>
      </li>
      <li class="nav-item logout-nav" @click="LogOut">
        <a
          class="nav-link"
          v-if="!!(login && login.token && login.token !== '')"
        >Logout</a>
      </li>
    </ul>
    <div class="store-btns d-flex align-items-center mt-4">
      <a href="https://itunes.apple.com/in/app/deonde/id1449714698?mt=8">
        <img src="@/assets/images/app-store.png" />
      </a>
      <a href="https://play.google.com/store/apps/details?id=com.deonde.ordering">
        <img src="@/assets/images/google-play.png" />
      </a>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters, mapState } from "vuex";
export default {
  data() {
    return {
      menus: []
    };
  },
  computed: {
    ...mapGetters("user",["login", "logout"]),
    ...mapGetters("setting",["settings"]),
    ...mapState({
        // privacy_policy_url: (state) => state.setting && state.setting.settings.pages[0].privacypolicy_url,
        // terms_condition_url: (state) => state.setting && state.setting.settings.pages[1].terms_url
    })
  },
  mounted() {
    this.sideMenu();
    // this.checkLogout();
  },
  methods: {
    ...mapActions("user",["checkLogout"]),
    LogOut() {
      this.checkLogout({
        user_id: this.login.user_id,
        devicetype: "I",
        devicetoken: this.login.token,
        unquie_id: "",
        vendor_id: window.location.pathname.split('/')[2],
        is_langauage: "E",
      }).then((data) => {
        if (data.code == 200) {
          if (confirm("Are you sure you want to logout?")) {
            window.localStorage.removeItem('vuex');
            this.$toasted.success("Logout successfully", { duration: 5000 });
            this.$router.push({ name: "sign-in" });
          }
        }
      });
    },
    sideMenu() {
      this.menus = [
        {
          title: "Home",
          link: "Home",
          show: true,
          condition: true,
        },
        {
          title: "Login",
          link: "sign-in",
          show: false, //!(this.login && this.login.token && this.login.token !== '')
          condition: true,
        },
        {
          title: "Address",
          link: "address",
          show: false, //!!(this.login && this.login.token && this.login.token !== '')
          condition: false,
        },
        {
          title: "Orders",
          link: "orders",
          show: false, //!!(this.login && this.login.token && this.login.token !== '')
          condition: false,
        },
        {
          title: "My Profile",
          link: "profile",
          show: false, //!!(this.login && this.login.token && this.login.token !== '')
          condition: false,
        },
        {
          title: "Promocode",
          link: "promo-code",
          show: true,
          condition: true,
        },
        {
          title: "FAQ",
          link: "faq",
          show: true,
          condition: true,
        },
      ];
    },
  },
};
</script>