<template>
<div>

  <nav-bar></nav-bar>
   <section class="vendor-details-main menu-details pb-4">
      <div class="container-fluid">
         <div class="row">
               <div class="col-md-9">
               <div class="vendor-left-main text-capitalize d-flex">
                  <div class="media w-100">
                     <img :src="resturantDetail.icon_image" class="mr-3" alt="...">
                     <div class="media-body">
                        <h3 class="mt-0">{{resturantDetail.name}}</h3>
                        <p class="mb-0">{{resturantDetail.location_address}}</p>
                        <rating :rating="5"></rating>
                     </div>
                     <div class="deliveryfees-main text-center d-flex align-content-center ml-auto mr-4">
                        <div class="delivery-fees p-3">
                           <h5 class="mb-0"><span v-html="currencys"></span>{{parseFloat(resturantDetail.minimum_order_value).toFixed(1)}}</h5>
                           <p class="mb-0">Minimum Order</p>
                        </div>
                        <div class="delivery-minutes p-3">
                           <h5 class="mb-0"><span v-html="currencys"></span>{{parseFloat(resturantDetail.two_person_price).toFixed(1)}}</h5>
                           <p class="mb-0">Value for Two</p>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </div>
      <cart></cart>        
    </section>
    <section class="resturant-menu-main">
        <div class="container-fluid">
            <div class="row">
               <div class="col-md-9 pr-0">
                    <item-list />
               </div>
            </div>
        </div>
    </section>
</div>
</template>

<script>
import NavBar from "@/components/NavBar.vue";
import Cart from '@/components/Cart.vue';
import Rating from '@/components/Rating.vue';
import ItemList from '@/components/ItemList.vue';
import { mapActions, mapGetters, mapState } from 'vuex';
export default {
    components: {NavBar,Cart, Rating, ItemList  },
    data() {
       return {
          resturantDetail: {},
          currencys: '',
       }
    },
    computed: {
       ...mapGetters("restaurant",["restaurantDetails"]),
       ...mapGetters("setting",["settings"]),
       ...mapState({
          currency: (state) => state.settings.currency,
         }),
    },
    mounted() {
       this.getRestaurantDetail();
       this.currencys = this.currency;
    },
    methods:{
       ...mapActions("restaurant",["getRestaurantDetails"]),
       getRestaurantDetail(){
          this.getRestaurantDetails({
               vendor_id: window.location.pathname.split('/')[2],
               restaurant_id: this.$route.params.restaurant_id,
               is_langauge : "en"
          }).then(data => {
               this.resturantDetail = data.Result;
          })
       }
    }
}
</script>

