<script>
import Item from '@/components/Item.vue'
export default {
    components: {Item},
    data(){
        return {
            menus: [
                { id: 1,name: "Khichadi Meal", items: [
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'},
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'},
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'},
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'},
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'}
                ] },
                { id: 2,name: "Indian Meal", items: [
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'}
                ] },
                { id: 3,name: "Chinese Meal", items: [
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'}
                ] },
                { id: 4,name: "Global Meal", items: [
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'}
                ] },
                { id: 5,name: "Momos", items: [
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'}
                ] },
                { id: 6,name: "Rice", items: [
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'}
                ] },
                { id: 7,name: "Desserts", items: [
                    {name: 'Item', description: 'Item Description', price: 120, image: '@/assets/images/food1.png'}
                ] },
            ]
        } 
    },
    mounted(){
        this.sections = document.querySelectorAll("section[id]");
        window.addEventListener("scroll", this.navHighlighter);
    },
    beforeUnmount(){
        window.removeEventListener("scroll", this.navHighlighter);
    },
    methods: {
        navHighlighter(){
            let scrollY = window.pageYOffset;
            this.sections.forEach(current => {
                const sectionHeight = current.offsetHeight;
                const sectionTop = current.offsetTop - 40;
                const sectionId = current.getAttribute("id");
                
                if (
                    scrollY > sectionTop &&
                    scrollY <= sectionTop + sectionHeight
                ){
                    document.querySelector(".navigation a[href*=" + sectionId + "]").classList.add("active");
                } else {
                    document.querySelector(".navigation a[href*=" + sectionId + "]").classList.remove("active");
                }
            });
        }
    }

};
</script>

